@use 'settings/colors' as *;
@use 'settings/mixins' as *;
@use 'settings/fonts' as *;

* {
  box-sizing: border-box;
}

body {
  min-width: 360px;
  background-color: $color-background-primary;
  color: $color-fonts-primary;
  font-family: $font-family-primary;
}

html {
  font-size: 62.5%;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;
  height: 100%;
  margin: 0 auto;

  @include tablet {
    padding: 0 15px;
  }

  @include desktop {
    width: 1352px;
    padding: 0 15px;
  }
}

.logo {
  display: inline-block;
  text-align: left;
  text-decoration: none;
  font-size: 3.2rem;
  font-family: $font-family-secondary;
  color: $color-accent-blue;
  font-weight: 300;
  padding: 10px 10px 0 10px;

  &--footer {
    font-size: 2.6rem;
  }
}

h1,
h2,
h3,
h4 {
  font-family: $font-family-secondary;
  color: $color-fonts-primary;
  font-weight: 400;
  text-align: center;
}

.opening__heading {
  font-size: 3.5rem;
}

.heading--secondary {
  font-size: 3rem;
  line-height: 3.5rem;
}
